import React, { Component } from 'react'
import './Form.css'

export default class Form extends Component {
    constructor(props) {
        super(props)
        this.state = {
            altura: 0,
            peso: 0,
            resultado: ''
        }

        this.calcIMC = this.calcIMC.bind(this)
    }

    handlePeso = (event) => this.setState({ peso: event.target.value })

    handleAltura = (event) => this.setState({ altura: event.target.value })

    calcIMC(e) {
        e.preventDefault()
        let imc = this.state.peso / (this.state.altura * this.state.altura)
        let s = this.state
        s.resultado = imc

        if (s.resultado < 18.5) {
            s.resultText = `Seu IMC é ${this.state.resultado.toFixed(2)} você está abaixo do peso`
        } else if (imc > 18.5 && imc < 24.9) {
            s.resultText = `Seu IMC é ${this.state.resultado.toFixed(2)} você está no seu peso normal`
        } else if (imc > 25.0 && imc < 29.9) {
            s.resultText = `Seu IMC é ${this.state.resultado.toFixed(2)} você está no sobrepeso`
        } else if (imc > 30.0 && imc < 39.9) {
            s.resultText = `Seu IMC é ${this.state.resultado.toFixed(2)} você está obeso`
        } else if (imc >= 40.0) {
            s.resultText = `Seu IMC é ${this.state.resultado.toFixed(2)} você está na obesidade grave`
        }

        this.setState(s)
    }
    render() {
        return (
            <div>
                <h1>Calcular IMC</h1>
                <form>
                    <input type="number" onChange={this.handlePeso} placeholder="Inserir seu peso" />
                    <input type="number" onChange={this.handleAltura} placeholder="Inserir sua altura" />
                    <input type="button" onClick={this.calcIMC} className="btn" value="Calcular IMC" />
                    <h4>{this.state.resultText}</h4>
                </form>
            </div>
        )
    }
}